import React, { useEffect, useState } from "react";
import "./Order.css";
import { Container, Table, Button, Form, Row, Col } from "react-bootstrap";
import productSolded from "../../services/productSolded";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import ActionTypes from "../../stores/action";
import { toast } from "react-toastify";

const Order = () => {
  
  const [listcheckout, setListcheckout] = useState([]);
  const [result, setResult] = useState("");
  const getUser = useSelector((state) => state.auth.dataUser);
  console.log("getUser", getUser);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  useEffect(() => {
    loadData();
  }, []);
  const loadData = () => {
    productSolded.getCheckout(getUser.userId).then((res) => {
      setListcheckout(res.data.data);
    });
  };
  const hanhdleCode = (e, list, id) => {
    console.log("list", list);
    dispatch({
      type: ActionTypes.DATA_USER_CHECK_OUT,
      dataUserCheckOut: list,
    });
    e.preventDefault();
    productSolded.getCodeOrder(list.codeOrder).then((res) => {
      if (res.data.errorCode === 0) {
        const x = [...res.data.data]
        dispatch({
          type: ActionTypes.DATA_CHECKOUT,
          dataCheckOut: {
            transportFee:list.transportFee,
            coupon: list.coupon,
            x
          }
        });
        navigate(`/order/${id}`);

      }
    });
  };
  console.log(listcheckout)
  const handleDelete = (id, e) => {
    e.preventDefault();
    toast.warning("n Hng Ca Bn ang c X");
    productSolded.remove(id).then((res) => {
      console.log(res.data);
      if (res.data.errorCode === 0) {
        setTimeout(() => {
          loadData();
          toast.success("n Hng Ca Bn  c Hy Thnh Cng");
        }, 6000);
      }
    });
  };
  return (
    <Container fuild="true" className="oder">
      <span
        className="hover-layout"
        style={{ cursor: "pointer" }}
        onClick={() => navigate("/feature")}
      >
        Thanh Ton
      </span>{" "}
      <i className="fa fa-angle-right mx-1 " aria-hidden="true"></i>
      <span className="hover-layout">n Hng</span>
      {/* <i className="fa fa-angle-right mx-1" aria-hidden="true"></i>
      <span>Chi Tit</span> */}
      <section
        className="ftco-section ftco-cart"
        style={{ paddingTop: "0 !important" }}
      >
        <div className="row">
          {listcheckout.length === 0 ? (
            <Row>
              {listcheckout.length === 0 ? (
                <h3 className="text-center mt-3 mb-3 text-capitalize">
                  bn khng c n hng no ,vui lng quay li{" "}
                  <span
                    className="text-primary text-uppercase fs-4 hover-layout"
                    style={{ cursor: "pointer" }}
                    onClick={() => navigate("/shop")}
                  >
                    ca hng
                  </span>
                </h3>
              ) : (
                ""
              )}
            </Row>
          ) : (
            <div
              className="col-md-12 ftco-animate"
              style={{ paddingTop: "0 !important" }}
            >
              <h4 className="text-center m-b-20">n hng ca ti</h4>
              <div className="cart-list">
                <table className="table table-light">
                  <thead className="thead-primary">
                    <tr className="text-center">
                      <th>M n hng</th>
                      <th>Tn khch hng</th>
                      <th>Ngy t</th>
                      <th>Trng thi</th>
                      <th>&nbsp;</th>
                    </tr>
                  </thead>

                  <tbody>
                    {listcheckout.map((list, index) => (
                      <tr className="text-center" key={index}>
                        <td className="product-name" width="10%">
                          <a
                            href="/#"
                            style={{ color: "blue" }}
                            onClick={(e) => hanhdleCode(e, list, index)}
                          >
                            {list.codeOrder}
                          </a>
                        </td>
                        <td className="price" width="20%">
                          {list.userName}
                        </td>
                        <td
                          className="product-name"
                          style={{ color: "#82ae46", " fontWeight": "bold" }}
                          id="status"
                          width="20%"
                        >
                          {list.create_at.slice(0, 10)}
                        </td>
                        <td
                          className="product-name"
                          style={{ color: "red", fontWeight: "bold" }}
                          id="status"
                          width="20%"
                        >
                          {list.status === 0 ? "New" :list.status === 1 ?" xc nhn":list.status === 2 ? "ang vn chuyn":'giao thnh cng'}
                        </td>
                        <td className="product-name">
                          {list.status === 0 ? <a
                            href="/order/deleteshop/{{ sc.id}}"
                            style={{ color: "blue" }}
                          // onclick="return confirm('Bn c chc mun hy n hng ny !!!')"
                          >
                            <span
                              id="huy"
                              onClick={(e) => handleDelete(list.id, e)}
                            >
                              Hy n Hng
                            </span>
                          </a> : <del id="huy">
                           
                       
                            Hy n Hng
                          </del>}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </div>
      </section>
    </Container>
  );
};

export default Order;
